.perform-action {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9; /* Light background for consistency */
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow */
    color: #333; /* Ensure text is dark enough to be visible */
}

.perform-action h2 {
    margin-bottom: 20px;
    color: #333; /* Dark text color for titles */
}

.nft-display-container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;
    flex-wrap: wrap;
}

.nft-display {
    flex: 1;
    text-align: center;
    padding: 10px;
    background-color: #fff; /* White background for clarity */
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow */
    color: #333; /* Dark text color */
}

.nft-details {
    margin-bottom: 10px;
}

.nft-image {
    width: 100%;
    height: auto;
    border-radius: 10px;
    transition: transform 0.2s ease-in-out;
}

.nft-image:hover {
    transform: scale(1.05);
}

.nft-info {
    margin-top: 10px;
    color: #333; /* Dark text color */
}

.nft-info p {
    margin: 5px 0;
}

select {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc; /* Light border for consistency */
    border-radius: 5px;
    background-color: #f0f0f0; /* Light gray background */
    transition: background-color 0.2s ease-in-out;
    color: #333; /* Dark text color */
}

select:hover {
    background-color: #e9e9e9;
}

select:focus {
    outline: 2px solid #007bff;
}

button {
    padding: 10px 20px;
    background-color: #007bff; /* Primary button color */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}

button:disabled {
    background-color: #dc3545; /* Disabled button color */
    cursor: not-allowed;
}

button.action-not-allowed {
    background-color: #dc3545;
    cursor: not-allowed;
}

button:hover:not(:disabled) {
    background-color: #0056b3;
}

button:focus {
    outline: 2px solid #007bff;
}

.loading-spinner {
    border: 4px solid #f3f3f3;
    border-top: 4px solid #007bff;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    animation: spin 2s linear infinite;
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.alert {
    padding: 15px;
    border-radius: 5px;
    margin-top: 20px;
    color: white;
    font-weight: bold;
}

.alert.default {
    background-color: #28a745; /* Success alert color */
}

.alert.destructive {
    background-color: #dc3545; /* Error alert color */
}

.progress {
    height: 8px;
    background-color: #f0f0f0; /* Light gray background */
    border-radius: 5px;
    overflow: hidden;
    margin-top: 10px;
}

.progress-bar {
    height: 100%;
    background: linear-gradient(90deg, #007bff, #0056b3);
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
    transition: width 0.2s ease-in-out;
}

.progress-label {
    margin-top: 5px;
    text-align: center;
    font-size: 0.9em;
    color: #555; /* Subtle text color */
}

.tooltip {
    position: relative;
    display: inline-block;
    cursor: pointer;
}

.tooltip .tooltiptext {
    visibility: hidden;
    width: 140px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 125%; 
    left: 50%;
    margin-left: -70px;
    opacity: 0;
    transition: opacity 0.3s;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}

.refill-button {
    margin-top: 10px;
    width: 100%;
    padding: 8px 16px;
    background-color: #4caf50; /* Refill button color */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}

.refill-button:hover:not(:disabled) {
    background-color: #45a049;
}

.refill-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

.progress-container {
    margin-top: 15px;
    padding-bottom: 10px;
}

.stake-button {
    margin-top: 10px;
    width: 100%;
    padding: 8px 16px;
    background-color: #f39c12; /* Staking button color */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}

.stake-button:hover:not(:disabled) {
    background-color: #e67e22;
}

.stake-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

@media (max-width: 768px) {
    .nft-display-container {
        flex-direction: column;
        gap: 10px;
    }
    .nft-display {
        width: 100%;
    }
}
