/* General Styles */
.App {
    text-align: center;
}

.App-logo {
    height: 80px;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.section {
    margin: 20px 0;
}

.error-message {
    color: red;
    font-weight: bold;
}

/* PerformAction Styles */
.perform-action {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 600px;
    margin: 20px auto;
}

.perform-action button {
    width: 100%;
    margin-bottom: 10px;
    padding: 10px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.perform-action button:hover {
    background-color: #45a049;
}

@media (max-width: 768px) {
  .perform-action button {
    font-size: 14px;
    padding: 8px 16px;
  }
}

button:disabled {
    background-color: red;
    color: white;
    cursor: not-allowed;
}

/* NFT Display Container */
.nft-display-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    gap: 20px;
    flex-wrap: wrap;
}

.nft-display {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 48%;
}

.nft-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
}

.nft-info {
    margin-top: 10px;
    text-align: center;
}

.nft-info p {
    margin: 2px 0;
    font-size: 14px;
}

.nft-display label {
    margin-bottom: 5px;
    font-weight: bold;
    text-align: center;
}

.nft-display select {
    width: 100%;
    padding: 8px;
    font-size: 14px;
    border-radius: 4px;
    border: 1px solid #ccc;
}

.nft-image {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 8px;
    border: 1px solid #ddd;
}

/* Status messages */
.status {
    font-size: 14px;
    margin-top: 10px;
}

.status.error {
    color: red;
}

.status.success {
    color: green;
}
